import React from 'react'
import { Button } from 'antd'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import LoginForm from './LoginForm'
import text from '../text'

let Login = ({language}) => {
    return (
        <div className='padded-container'>
            <div className='auth-container'>
                <div className="auth-left-content">
                    <img className='title-icon' src="/img/register/profile.png" alt="Perfil"/>
                    <p className='title'>{text.login.portalTitle[language]}</p>
                    <p className='subtitle'>{text.login.portalSubtitle[language]}</p>
                    <br/>
                    <LoginForm/>
                    <div className="auth-bottom-text">
                        <p>{text.form.forgotPassword[language]} <Link to='/recover-password'>{text.form.forgotPasswordLink[language]}</Link></p>
                        <p>{text.form.register[language]} <Link to='/register'>{text.form.registerLink[language]}</Link></p>
                    </div>
                </div>
                <div className="auth-right-content">
                <img className='title-icon' src="/img/register/professional.png" alt="Perfil"/>
                    <p className='title'>{text.roles.agent[language]} / {text.roles.serviceProvider[language]}</p>
                    <p className='login-info-text'>
                        Lorem ipsum dolor sit amet consectetur, adipisicing elit. 
                        Alias placeat, veritatis magni dolore itaque non totam! 
                        Vero, vitae? Nostrum eveniet accusantium maiores quis 
                        alias possimus eaque itaque ipsam repellendus! Iure?
                        Lorem ipsum dolor sit, amet consectetur adipisicing elit. 
                        Possimus accusamus error laboriosam. Explicabo, ad ipsa corrupti 
                        voluptas officia facilis. Expedita odio modi est non ratione. 
                        Iure esse maiores dolore accusamus.
                        <br/><br/>
                        Dicta, illo numquam? Ad possimus debitis nam nostrum adipisci 
                        laudantium eveniet, vitae hic, suscipit facilis architecto 
                        molestias nihil placeat cumque et vero at fuga! Maxime non ea 
                        fugit ratione dolore? Quis eos odit accusantium eum facilis accusamus pariatur non 
                    </p>
                    <Button className='nav-button' type='primary'>{text.roles.agent[language]}</Button>
                    <Button className='nav-button' type='primary'>{text.roles.serviceProvider[language]}</Button>
                </div>
            </div>
        </div>
    )
}

let mapState = ({
    app: {language}
}) => ({
    language
})

export default connect(
    mapState,
    {
        
    }
)(Login)