import React from 'react'
import { connect } from 'react-redux'
import text from '../text'
import BasicData from './registerComponents/BasicData'

let RegisterForm = ({language}) => {

    return (
        <div className='register-container'>
            <div style={{backgroundImage: 'linear-gradient(to bottom, #F9C799AA, #FFFFFF00), url(/img/register/bg2.png)'}} className="left-register">
                <img src="/img/common/logo-white.png" alt="punta piedra logo"/>
            </div>
            <div className="right-register-block">
                <img className='title-icon' src="/img/register/profile.png" alt="Perfil"/>
                <p className='title'>{text.register.portalTitle[language]}</p>
                <p className='subtitle'>{text.register.portalSubtitle[language]}</p>
                <BasicData/>
            </div>
        </div>
    )
}

let mapState = ({
    app: {language}
}) => ({
    language
})

export default connect(
    mapState,
    {

    }
)(RegisterForm)