import React from 'react'
import { connect } from 'react-redux'
import { Spin } from 'antd'

let Home = ({fetching, name}) => {

    return (
        <div className='padding-container home-container'>
            {
                fetching ? 
                    <div className='spinner-full-page'><Spin size='large'/></div>
                    :
                    <h1>Punta Piedra {name}</h1>
            }
        </div>
    )
}

let mapState = ({user: {fetching, info: {name}}}) => ({fetching, name})

export default connect(
    mapState,
    {
        
    }    
)(Home)