import { Button, Checkbox, Input, Spin } from 'antd'
import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { getDataAction, handleInfoAction } from '../../redux/dataDuck'
import text from '../text'

const Profile = ({info, fetching, name, language, getData, handleInfo}) => {

    useEffect(() => {
        getData()
    }, [])

    return (
        <div className='application-container'>
             {
                fetching ? 
                    <div className='spinner-full-page'><Spin size='large'/></div>
                    :
                    <div className="application-content">
                        <div className="app-top-icon">
                            <img src="/img/sidebar/profile.svg" alt="profile"/>
                            <p>{text.sidebar.profile[language]}</p>
                        </div>
                        <h2>{text.sidebarSubtitle.profile[language]}</h2>
                        <div className='padded-app-container form-container'>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.name} name='name' placeholder={text.register.firstName[language]}/>
                                <Input className={`${language === 'english' && 'last-input'}`} onChange={handleInfo} value={info.lastName} name='lastName' placeholder={text.register.lastName[language]}/>
                                {language === 'espa√±ol' && <Input required onChange={handleInfo} value={info.secondLastName} name='secondLastName' placeholder={text.register.secondLastName[language]}/>}
                            </div>
                            <Input id='company-app' onChange={handleInfo} value={info.company} name='company' placeholder={text.register.company[language]}/>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.email} name='email' placeholder={text.register.email[language]}/>
                                <Input className='last-input' onChange={handleInfo} value={info.phone} name='phone' placeholder={text.register.phone[language]}/>
                            </div>
                            <h2 className='beneficiary-message'>{text.profile.beneficiaryTitle[language]}</h2>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.beneficiaryName} name='beneficiaryName' placeholder={text.register.firstName[language]}/>
                                <Input className={`${language === 'english' && 'last-input'}`} onChange={handleInfo} value={info.beneficiaryLastName} name='beneficiaryLastName' placeholder={text.register.lastName[language]}/>
                                {language === 'espa√±ol' && <Input onChange={handleInfo} value={info.beneficiarySecondLastName} name='beneficiarySecondLastName' placeholder={text.register.secondLastName[language]}/>}
                            </div>
                            <div className="multiple-inputs">
                                <Input onChange={handleInfo} value={info.beneficiaryEmail} name='beneficiaryEmail' placeholder={text.register.beneficiaryEmail[language]}/>
                                <Input className='last-input' onChange={handleInfo} value={info.beneficiaryPhone} name='beneficiaryPhone' placeholder={text.register.beneficiaryPhone[language]}/>             
                            </div>
                        </div>
                        <div className="checkbox-input">
                            <Checkbox
                                checked={info.consent}
                                onChange={handleInfo}
                            >
                                {text.register.confirm[language]}
                            </Checkbox>
                        </div>
                        <Button loading={fetching} className='nav-button'>{text.button.update[language]}</Button>
                    </div>
            }
        </div>
    )
}

let mapState = ({
    app: {language},
    user: {fetching, info: {name}},
    data: {info}
}) => ({
    language,
    fetching,
    name,
    info
})

export default connect(
    mapState,
    {
        getData: getDataAction,
        handleInfo: handleInfoAction
    }
)(Profile)
