import { Cookies } from "react-cookie"
import { getData } from "../services/api"

let initialData = {
    info: {},
    fetchingData: false,
    error: {}
}

let GET_DATA = 'GET_DATA'
let GET_DATA_SUCCESS = 'GET_DATA_SUCCESS'
let GET_DATA_ERROR = 'GET_DATA_ERROR'

let reducer = (state = initialData, action) => {
    switch(action.type){
        case GET_DATA:
            return {...state, fetchingData: true, info: {}, error: {}}
        case GET_DATA_SUCCESS:
            return {...state, fetchingData: false, info: action.payload}
        case GET_DATA_ERROR:
            return {...state, fetchingData: false, error: action.payload}
        default:
            return {...state}
    }
}

export default reducer

export let handleInfoAction = () => dispatch => {
    
}

export let getDataAction = () => dispatch => {
    let id = new Cookies().get('id')
    if(!id) return dispatch({
        type: GET_DATA_ERROR,
        payload: {status: 403}
    })
    dispatch({
        type: GET_DATA
    })
    getData({id})
        .then(res => dispatch({
            type: GET_DATA_SUCCESS,
            payload: res.data
        }))
        .catch(err => dispatch({
            type: GET_DATA_ERROR,
            payload: err.response
        }))
}