import { Cookies } from "react-cookie"

let initialData = {
    language: new Cookies().get('language') || 'english'
}

let CHANGE_LANG = 'CHANGE_LANG'

let reducer = (state = initialData, action) => {
    switch(action.type){
        case CHANGE_LANG:
            return {...state, language: action.payload}
        default:
            return {...state}
    }
}

export default reducer

export let changeLangAction = () => (dispatch, getState) => {
    let { language } = getState().app
    new Cookies().set('language', language === 'english' ? 'espa√±ol' : 'english')
    dispatch({
        type: CHANGE_LANG,
        payload: language === 'english' ? 'espa√±ol' : 'english'
    })
}