import React, { useEffect, useState } from 'react'
import { Input, Button, message, Select, Checkbox } from 'antd'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { signupAction } from '../../../redux/userDuck'
import text from '../../text'
let { Option } = Select

let BasicData = ({fetching, language, error, info, signup, history}) => {

    let [data, setData] = useState({
        type: 'buyer'
    })

    let handleData = e => {
        setData({...data, [e.target.name]: e.target.value})
    }

    let handleSubmit = () => {
        if(!data.name ||
             !data.lastName ||
             (!data.secondLastName && language === 'espa√±ol') ||
             !data.company ||
             !data.email ||
             !data.phone
            ) 
            return message.warning(text.messages.missingFields[language])
        let rex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if(!rex.test(data.email.toLowerCase())) return message.warn(text.messages.incorrectEmail[language])
        if(!data.password || data.password.length < 6) return message.warn(text.messages.incorrectPassword[language])
        if(data.password !== data.repeatPassword) return message.warn(text.messages.repeatedPasswordError[language])
        if(!data.consent) return message.warn(text.messages.consent[language])
        signup(data)
    }

    useEffect(() => {
        if(error.status === 500) return message.error('Ingresa todos los campos')
        if(info.name) return history.push('/profile')
    }, [error, info])

    return (
        <div className='form-container'>
            <div className="multiple-inputs">
                <Input onChange={handleData} value={data.name} name='name' placeholder={text.register.firstName[language]}/>
                <Input className={`${language === 'english' && 'last-input'}`} onChange={handleData} value={data.lastName} name='lastName' placeholder={text.register.lastName[language]}/>
                {language === 'espa√±ol' && <Input required onChange={handleData} value={data.secondLastName} name='secondLastName' placeholder={text.register.secondLastName[language]}/>}
            </div>
            <Input onChange={handleData} value={data.company} name='company' placeholder={text.register.company[language]}/>
            <div className="multiple-inputs">
                <Input onChange={handleData} value={data.email} name='email' placeholder={text.register.email[language]}/>
                <Input className='last-input' onChange={handleData} value={data.phone} name='phone' placeholder={text.register.phone[language]}/>
            </div>
            <div className="selector-input">
                <p>{text.register.beneficiary[language]}</p>
                <Select onChange={value => setData({...data, beneficiary: value})} value={data.beneficiary} defaultValue='no'>
                    <Option value='yes'>{text.responses.yes[language]}</Option>
                    <Option value='no'>{text.responses.no[language]}</Option>
                </Select>
            </div>
            {
                data.beneficiary === 'yes' &&
                    <>
                        <small className='beneficiary-title'>{text.register.beneficiaryTitle[language]}</small>
                        <div className="multiple-inputs">
                            <Input onChange={handleData} value={data.beneficiaryName} name='beneficiaryName' placeholder={text.register.firstName[language]}/>
                            <Input className={`${language === 'english' && 'last-input'}`} onChange={handleData} value={data.beneficiaryLastName} name='beneficiaryLastName' placeholder={text.register.lastName[language]}/>
                            {language === 'espa√±ol' && <Input onChange={handleData} value={data.beneficiarySecondLastName} name='beneficiarySecondLastName' placeholder={text.register.secondLastName[language]}/>}
                        </div>
                        <div className="multiple-inputs">
                            <Input onChange={handleData} value={data.beneficiaryEmail} name='beneficiaryEmail' placeholder={text.register.beneficiaryEmail[language]}/>
                            <Input className='last-input' onChange={handleData} value={data.beneficiaryPhone} name='beneficiaryPhone' placeholder={text.register.beneficiaryPhone[language]}/>             
                        </div>
                    </>
            }
            <Select className='agent-selector' onChange={value => setData({...data, customerAgent: value})} placeholder={text.register.selectAgent[language]} value={data.customerAgent} defaultValue={data.customerAgent}>
                    <Option value='agent1'>Gerardo Jim√©nez</Option>
                </Select>
            <Input.Password onChange={handleData} value={data.password} name='password' placeholder={text.register.password[language]}/>
            <Input.Password onChange={handleData} value={data.repeatPassword} name='repeatPassword' placeholder={text.register.repeatPassword[language]}/>
            <div className="checkbox-input">
                <Checkbox
                    checked={data.consent}
                    onChange={e => setData({...data, consent: e.target.checked})}
                >
                    {text.register.confirm[language]}
                </Checkbox>
            </div>
            <Button loading={fetching} onClick={handleSubmit} className='nav-button'>{text.button.register[language]}</Button>
            <div style={{marginTop: '1rem'}}>
                <Button type='ghost'><Link to='/register'>{text.button.return[language]}</Link></Button>
            </div>
            <div className="auth-bottom-text">
                <p>{text.register.login[language]} <Link to='/login'>{text.register.loginLink[language]}</Link></p>
            </div>
        </div>
    )
}

let mapState = ({
    user: {fetching, error, info},
    app: {language}
}) => ({
    fetching,
    error, 
    info,
    language
})

export default connect(
    mapState,
    {
        signup: signupAction
    }
)(BasicData)