import React from 'react'
import { connect } from 'react-redux'
import { changeLangAction } from '../../redux/appDuck'
import text from '../text'

const AuthNav = ({language, toggleLanguage}) => {
    return (
        <div className='auth-nav'>
            <div className="top-nav">
                <img src="/img/navbar/lupa.svg" alt="lupa"/>
                <img src="/img/navbar/notify.svg" alt="notify"/>
                <img src="/img/navbar/profile.svg" alt="profile"/>
                <p className='language-selector' onClick={toggleLanguage}>
                    {language}
                </p>
                <p>Logout</p>
            </div>
            <div className="left-nav">
                <img className='sidebar-logo' src="/img/common/logo-dark.png" alt="PP logo black"/>
                <div className="sidebar-nav">
                    <div className="sidebar-element">
                        <img src="/img/sidebar/profile-gray.svg" alt="nav element"/>
                        <p>{text.sidebar.profile[language]}</p>
                    </div>
                    <div className="sidebar-element">
                        <img src="/img/sidebar/chooseHouse-gray.svg" alt="nav element"/>
                        <p>{text.sidebar.chooseHouse[language]}</p>
                    </div>
                    <div className="sidebar-element">
                        <img src="/img/sidebar/documents-gray.svg" alt="nav element"/>
                        <p>{text.sidebar.documentation[language]}</p>
                    </div>
                    <div className="sidebar-element">
                        <img src="/img/sidebar/finance-gray.svg" alt="nav element"/>
                        <p>{text.sidebar.financing[language]}</p>
                    </div>
                    <div className="sidebar-element">
                        <img src="/img/sidebar/intDesign-gray.svg" alt="nav element"/>
                        <p>{text.sidebar.interiorDesign[language]}</p>
                    </div>
                    <div className="sidebar-element">
                        <img src="/img/sidebar/ownerServ-gray.svg" alt="nav element"/>
                        <p>{text.sidebar.propietaryService[language]}</p>
                    </div>
                    <div className="sidebar-element">
                        <img src="/img/sidebar/faq-gray.svg" alt="nav element"/>
                        <p>{text.sidebar.faq[language]}</p>
                    </div>
                    <div className="sidebar-element">
                        <img src="/img/sidebar/help-gray.svg" alt="nav element"/>
                        <p>{text.sidebar.help[language]}</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

let mapState = ({
    app: {language}
}) => ({
    language
})

export default connect(
    mapState,
    {
        toggleLanguage: changeLangAction
    }
)(AuthNav)
